@import url(https://fonts.googleapis.com/css2?family=Bree+Serif&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
.backImage{
    width:100%;
    height:20vw;
    min-height: 28vh;
    max-height:40vh;
    object-fit:cover;
    margin-top: 0px;
}


/*///////////........HOME.........///////// */

.btn-lookup{
border: none;
background: none;
cursor: pointer;
}
.btn-lookup:focus{
 outline: none;
}
.lookUp-container{
    display:flex;
    justify-content:center;
    align-items:center;
    position:-webkit-sticky;
    position:sticky;    
    top:60px;
    z-index:800;
    background-color:white;
    width:100%;
    height:67px;
}
.lookUp{   
height:39x;
border: 1px solid black;
background-color:white;
border-radius: 27px;
width: 60%;  
max-width: 500px;
display: flex;
align-items: center;    
justify-content:center;
}
.imput-lookup{
width:82%;
margin-left:5px;
border: none;
background: none;
}
.imput-lookup:focus{
    outline: none;
    outline-width: 0;
}

.list-container{
    margin-top:2vw;
    display:flex;
    justify-content: center;
    padding-bottom:40px;
}

.list{    
    width: 85vw;
    display:flex;
    justify-content: center;
    flex-wrap: wrap;
}

.card{
    height:330px;
    width:250px;    
    margin: 12px 12px 12px 12px;
    box-shadow: 1px 1px 4px gray;
    border-radius: 10px;
    transition: transform 0.5s;    
}
.card-container{
    position:relative;
    height:100%;
}

.card:hover{
    transform: scale(1.01);
}
.card-image{
    width:250px;
    height:200px;
    border-radius: 10px 10px 0 0;
}
.card-country{
    margin-left: 7%;
    margin-top: 10px;
    margin-bottom:0px;
    color:rgb(99, 98, 98)
}
.card-city{
    margin-left: 7%;
    margin-top: 0px;  
    margin-bottom: 0px;  
    font-family: 'Bree Serif', serif;    
    color:rgb(29, 43, 146);
    font-size:20px;       
}
.card-price{
    display:flex;    
    margin: 20px 0 0 7%;
    color: rgb(0, 92, 15);
    text-shadow: 1px 0px rgb(48, 48, 6);
}
.card-usd{
    color:black;
    margin-left:5px;
}
.card-add{
    position:absolute;
    width:60px;
    height:45px;
    right:20px;
    bottom:20px;
    background-color: rgb(2, 8, 19);    
    border-radius:10px;
    color:white;
    display:flex;
    align-items: center;
    justify-content:center;
    font-weight: bold;  
}
.card-add:hover{
    cursor:pointer;
    background-color: rgb(84, 85, 89); 
}
.card-added{
    position:absolute;
    top: 0px;
    left:16%;
    background-color:black;    
    color:white;
    width:170px;
    height:35px;
    display:flex;
    justify-content: center;
    align-items: center;
    border-radius: 0 0 10px 10px;
}
.hide-add{
    display:none;
}
.price-n{
    color:rgb(29, 43, 146);
    font-weight:bold;
    font-size:18px;
}
.price-container{
    display:flex;
    justify-content:center;    
}
.price-lookup{
    display:flex;    
    justify-content:space-between;
    align-items:center;
    width:320px;    
}
.price-lookup input{
    height:20px;
    border-radius: 10px;
}
.list-btn{ 
    display:none;
}
@media screen and (min-width: 660px) {

.lookUp-container{
    position:relative;
    top: 0;
    z-index:0;
 }
}
@media screen and (min-width: 760px) {

.list-btn{        
    transform: scale(7);
    display:block;
    cursor:pointer;
    margin: 0 55px 0 55px;
    color: rgb(1, 1, 61);
}
.list{
    flex-wrap: nowrap;
    height:400px;
    flex-direction:row;
    justify-content: start;
    width:65vw;  
    overflow:hidden;
    scroll-behavior: smooth;
}
.list-container{
    margin-top:0;
    align-items: center;
}
.card{
    margin: 35px 25px 0 0;
}

}


/* .....//////FOOTER/////.......... */

.footer{
    height: 25px;
    background: linear-gradient(to right,rgb(108, 193, 242),rgb(84, 115, 253));
    position:absolute;
    width:100%;
    bottom:0;
    display:flex;
    justify-content:flex-end;
    align-items: center;        
}
.footer p{
    font-size:10px;
    margin-right:40px;
    color:white;    
}
.container{
    height:100%;
    min-height:100vh;
    position:relative
}

.about{
    display:flex;    
    justify-content:center;
    margin-top:60px;  
}
.about p {
    width:70vw;   
    color:rgb(89, 86, 86); 
    font-size:22px;
    font-weight: bold;
    font-family: 'Bree Serif', serif; 
}
.about span{
    color:blue;
}
.navbar {
    background: linear-gradient(to right,rgb(108, 193, 242),rgb(84, 115, 253));
    height: 60px;
    width:100%;
    display: flex;    
    justify-content: space-between;
    align-items:center;
    position:-webkit-sticky;
    position:sticky;
    top:0;
    z-index: 1001;
  }

.logo{
    display:flex;
    flex-direction:row;
    justify-content: space-between;
    align-items:center;
    width:210px;
    color:white;
    text-shadow: -1px -1px 0 #000, 1px -1px 0 #000,-1px 1px 0 #000,1px 1px 0 #000;
    font-family: 'Roboto', sans-serif;
    font-size: 17px;  
    z-index:100;  
    text-decoration: none;
}

.icon{
width:45px;
height:40px;
margin-left:20px;     
} 

.menu-bars {
margin-right: 29px;
font-size: 2rem;  
color: white;  
}

.nav-menu {
background: linear-gradient(to top,rgb(152, 215, 252),rgb(84, 115, 253));
width: 200px;
height: 100vh;
display: flex;    
position: absolute;
top: 0;
right:0px;;
transition: 800ms;
z-index: 100;
} 

.hide{
    display:none;
}


.links{
list-style: none;
display: flex;
flex-direction:column;
}

.nav-text {
display: flex;    
align-items: center;
padding: 8px 0px 8px 16px;    
height: 60px;

}
.nav-links {
text-decoration: none;
color: #f5f5f5;
display:flex;
justify-content:space-between;
width:100px;
font-size: 20px;
text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
}


.nav-text span:hover {
color: gray;
}

.nav-menu-items {
width: 100%;
}

.navbar-cross {    
width: 100%;
height: 80px;
display:flex;
justify-content: flex-end;
align-items: center;
}

.cross {    
margin-right: 20px;
}

@media screen and (min-width: 760px) {
.logo{
position:absolute;
z-index:1006;
}

.menu-bars{
display:none;
}
.navbar-cross{
    display:none
}
.nav-menu {      
    background: linear-gradient(to right,rgb(108, 193, 242),rgb(84, 115, 253));
    display: flex;           
    top: 0px;
    right:0px;
    transition: 0ms;
    height:60px;
    position:relative;
    width:100%;
    z-index:1005;
    }
.hide{
    display:flex;
    position:relative;
    flex-direction:row;
    height:auto;   
    width:100%;    
}

.nav-menu-items {
width: default;
display:flex;
justify-content:flex-end;
}

.links{      
display: flex;
flex-direction:row-reverse;
justify-content: space-evenly; 
margin-top:0px;
margin-bottom: 0px; 
width:60%;
}
.nav-text {         
align-items: center;
padding: 0px 0px 0px 0px;   
height: 60px;                
}
.nav-links {     
display:flex;
justify-content: space-around;
width:auto;
font-size: 18px;
}
.nav-links span {     
margin-right: 10px;
}




}
/*///////////........CART.........///////// */

.cart-container{
    display:flex;
    flex-direction:column;
    height:auto;    
    justify-content: center;
    align-items:center;
}
.cart-item-container{
    display:flex;
    flex-direction:column;
    width:100%;
    justify-content: center;
    align-items:center;
}
.cart-item{
    display:flex;
    margin-top:15px;
    margin-bottom:15px;
    width:100%;
    min-width:300px;
    justify-content: space-around;
    align-items: center;
}
.cart-image{
    width:85px;
    height:65px;
    border-radius: 10px;
    object-fit:cover;
}
.cart-city{
    width:50px;
    font-weight:bold;
    font-family: 'Bree Serif', serif; 
    font-size:15px;
    color:rgb(29, 43, 146);
}
.cart-price{
    color: rgb(0, 92, 15);
    text-shadow: 1px 0px rgb(48, 48, 6);
    width:190px;
    font-size:15px;
    display:flex;
    justify-content:space-around;
    align-items:center;
}
.cart-lines{
    border: 1px dashed #000;
    width:80%;
}
.cart-purchase{
    padding-top:30px;
    display:flex;
    margin-bottom:35px;
}
.cart-btn{
    color:white;
    background-color:rgb(29, 43, 146);
    width:100px;
    height:60px;
    display:flex;
    align-items:center;
    justify-content:center;
    cursor:pointer;
}
.cart-delete{
    width:50px;
    height:40px;
    cursor:pointer;
    color:rgb(209, 0, 0);
}
.cart-noFound{
    display:flex;
    justify-content:center;
    margin-top:60px;
    font-weight: bold;
    font-family: 'Bree Serif', serif; 
    color:rgb(29, 43, 146);
    font-size:27px;
}

@media screen and (min-width: 560px) {
    .cart-image{
        width:160px;
        height:130px;       
    }
    .cart-city{
        width:100px;
        font-size:25px;
    }
    .cart-price{
        font-size:25px;
        width:250px;
    }    

}
